import { is } from 'ramda'

export default class User {
  constructor (user = {}) {
    this.id = is(Number, user.id) ? user.id : 0
    this.fullName = is(String, user.fullName) ? user.fullName : ''
    this.phone = is(String, user.phone) ? user.phone : ''
    this.identificationNumber = is(String, user.identificationNumber) ? user.identificationNumber : ''
    this.email = is(String, user.email) ? user.email : ''
    this.password = is(String, user.password) ? user.password : ''
    this.token = is(String, user.token) ? user.token : ''
    this.country = is(String, user.country) ? user.country : ''
    this.city = is(String, user.city) ? user.city : ''
  }
}
